<template>
  <div class="admin-page">
    <el-row>
      <el-col :span="24">
        <el-card class="box-card">
          <div slot="header" class="clearfix header">
            <span></span>
          </div>
          <el-tabs type="card">
            <el-tab-pane label="">
              <div class="tab-content post-message">
                <el-form ref="form" :model="form" label-width="120px">
                  <el-form-item label="">
                    <el-input v-model="form.title"></el-input>
                  </el-form-item>
                  <el-form-item label="">
                    <el-input type="textarea" v-model="form.content"></el-input>
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="handlePostMessage"></el-button>
                  </el-form-item>
                </el-form>
              </div>
            </el-tab-pane>

            <el-tab-pane label="">
              <div class="tab-content manage-users">
                <el-button type="primary" @click="handleAddUser"></el-button>
                <el-table :data="users" style="width: 100%">
                  <el-table-column prop="name" label="" width="150"></el-table-column>
                  <el-table-column prop="role" label="" width="100"></el-table-column>
                  <el-table-column prop="email" label="" width="200"></el-table-column>
                  <el-table-column label="" width="180">
                    <template slot-scope="scope">
                      <el-button size="mini" @click="handleEditUser(scope.row)"></el-button>
                      <el-button size="mini" type="danger" @click="handleDeleteUser(scope.row)"></el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </el-tab-pane>

            <el-tab-pane label="">
              <div class="tab-content system-logs">
                <el-table :data="logs" style="width: 100%">
                  <el-table-column prop="timestamp" label="" width="180"></el-table-column>
                  <el-table-column prop="level" label="" width="100"></el-table-column>
                  <el-table-column prop="message" label=""></el-table-column>
                </el-table>
              </div>
            </el-tab-pane>

            <el-tab-pane label="">
              <div class="tab-content system-settings">
                <el-form ref="settingsForm" :model="settingsForm" label-width="120px">
                  <el-form-item label="1">
                    <el-input v-model="settingsForm.param1"></el-input>
                  </el-form-item>
                  <el-form-item label="2">
                    <el-input v-model="settingsForm.param2"></el-input>
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="handleSaveSettings"></el-button>
                  </el-form-item>
                </el-form>
              </div>
            </el-tab-pane>

            <el-tab-pane label="">
              <div class="tab-content detection-records">
                <el-table :data="detectionRecords" style="width: 100%">
                  <el-table-column prop="filename" label="" width="180"></el-table-column>
                  <el-table-column prop="detectionTime" label="" width="180"></el-table-column>
                  <el-table-column prop="result" label=""></el-table-column>
                </el-table>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        title: '',
        content: ''
      },
      users: [
        { name: 'user1', role: 'admin', email: 'user1@example.com' },
        { name: 'user2', role: 'user', email: 'user2@example.com' }
      ],
      logs: [
        { timestamp: '2024-06-24 10:00:00', level: 'info', message: '' },
        { timestamp: '2024-06-24 10:05:00', level: 'error', message: '' }
      ],
      settingsForm: {
        param1: 'value1',
        param2: 'value2'
      },
      detectionRecords: [
        { filename: 'file1.mp4', detectionTime: '2024-06-24 10:00:00', result: '' },
        { filename: 'file2.jpg', detectionTime: '2024-06-24 09:45:00', result: '' }
      ]
    };
  },
  methods: {
    handlePostMessage() {
      // 
      console.log(':', this.form);
    },
    handleAddUser() {
      // 
      console.log('');
    },
    handleEditUser(user) {
      // 
      console.log(':', user);
    },
    handleDeleteUser(user) {
      // 
      console.log(':', user);
    },
    handleSaveSettings() {
      // 
      console.log(':', this.settingsForm);
    }
  }
};
</script>

<style scoped>
.admin-page {
  padding: 20px;
  background-color: #f5f5f5;
}

.header {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  color: #409EFF;
}

.tab-content {
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.post-message .el-form-item__label {
  color: #409EFF;
}

.manage-users .el-button--primary {
  background-color: #67C23A;
  border-color: #67C23A;
}

.system-logs .el-table__header-wrapper {
  background-color: #E6A23C;
}

.system-settings .el-form-item__label {
  color: #F56C6C;
}

.detection-records .el-table__header-wrapper {
  background-color: #909399;
}

.el-tabs__item.is-active {
  color: #409EFF !important;
  font-weight: bold;
}
</style>



