export default{
  state: {
    isCollapse:false ,// 
    tabsList:[
      {
        path: '/',
        name: 'FirstPage',
        label: '',
        icon: 's-home',
        url: 'FirstPage'

      }
    ]

  },
  mutations: {
    collapseMenu(state){
      state.isCollapse = !state.isCollapse
    },
    // 
    selectMenu(state,val){
      if( val.name !== 'main'){
//   
        const index = state.tabsList.findIndex(item=> item.name ===val.name)
        if(index === -1){
// 
          state.tabsList.push(val)
        }
      }
    },
    closeTag(state,val){
      // tabs
      const index = state.tabsList.findIndex(item=> item.name ===val.name)
      state.tabsList.splice(index,1)
    }
  }
}



